import { Fragment, useState } from 'react'
// import { useHistory } from 'react-router-dom'
import classnames from 'classnames'

import { useForm} from 'react-hook-form' //Controller
 import Select from 'react-select'
import { isObjEmpty, selectThemeColors } from '@utils' //selectThemeColors
import { addQuestion } from '../../Quiz/store/action'
import Repeater from '@components/repeater'
import { Label, FormGroup, Row, Col, Button, Form, Input } from 'reactstrap'
import { Plus } from 'react-feather'
import { useSelector, useDispatch } from 'react-redux'
import UiLoader from '@components/ui-loader'
const colourOptions = [
  { value: 'winnerselection', label: 'Winner Selection' },
  { value: 'Multiplechoice', label: 'Multiple choice' },
  { value: 'scorepredictor', label: 'Score Predictor' }
]
const AddQuestions = ({ stepper }) => {
  const { register, errors, handleSubmit, trigger} = useForm()
  // const history = useHistory()
  const store = useSelector(state => state.quiz)
  //const [check, setCheck] = useState(false)
  const { addedQuizID, addedQuizID2, lang_id, langs, eng_question, lang_count, addloader } = store
  
  const [multi, setMulti] = useState(false)
  const [scorewinner, setScorewinner] = useState(false) 
  const dispatch = useDispatch()
  const onSubmit = () => {
    trigger()
    if (isObjEmpty(errors)) {
      stepper.next()
    }
}

console.log("id", lang_id)
console.log(langs)
const [count, setCount] = useState(1)
let che = 0

const increaseCount = () => {

  if (count === 7) return
  setCount(count + 1)
  che = count
  
}
console.log(che)
const editcategory = (val) => {

  console.log(val.target.id)
  if (val.value === ('winnerselection' && che === count) || (val.value === 'scorepredictor' && che === count)) {
    
    setMulti(false)
    setScorewinner(true)
  } else if (val.value === 'Multiplechoice') {       
    setScorewinner(false)
    setMulti(true)
  } else {    
   
    setScorewinner(false)
    setMulti(false)
  }
}
// let ch = -1
console.log(eng_question.map(ques => ques.quest))
  const submitQuestion = (id_1, id_2) => {
   
    //setCheck(true)
   
    console.log(addedQuizID)
    trigger().then((res) => {
      if (res) {
        if (lang_count === 2) {
          const param = {
            quiz_id: addedQuizID,
            translation_quiz_questions: [
              {
                  master_language_id: 1,
                  translation_quiz_question: document.getElementById(`question-${id_1}`).value
                },
              {
                master_language_id: 2,
                translation_quiz_question: document.getElementById(`question-${id_2}`).value
              }
            ],
            quiz_question_duration: '30',
            quiz_question_sno: id_1,
            quiz_question_answers: [
              {
                translation_quiz_answers:[
                  {
                    // master_language_id:document.getElementById('quiz_lang').value,
                    master_language_id:1,
                    translation_quiz_answer: document.getElementById(`answer1-${id_1}`).value
                  },
                  {
                      // master_language_id:document.getElementById('quiz_lang').value,
                      master_language_id:2,
                      translation_quiz_answer: document.getElementById(`answer1-${id_2}`).value
                    }
                ],
              quiz_answer_sno: '1',
             quiz_answer_correct: '1'

             },
            {
              translation_quiz_answers:[
                {
                  master_language_id:1,
                  translation_quiz_answer: document.getElementById(`answer2-${id_1}`).value
                },
                {
                  master_language_id:2,
                  translation_quiz_answer: document.getElementById(`answer2-${id_2}`).value
                }
              ],
            quiz_answer_sno: '2',
           quiz_answer_correct: '0'
           },
           {
            translation_quiz_answers:[
              {
                master_language_id:1,
                translation_quiz_answer: document.getElementById(`answer3-${id_1}`).value
              },
              {
                  master_language_id:2,
                  translation_quiz_answer: document.getElementById(`answer3-${id_2}`).value
                }
            ],
          quiz_answer_sno: '3',
         quiz_answer_correct: '0'
         },
         {
          translation_quiz_answers:[
            {
              master_language_id:1,
              translation_quiz_answer: document.getElementById(`answer4-${id_1}`).value
            },
            {
              master_language_id:2,
              translation_quiz_answer: document.getElementById(`answer4-${id_2}`).value
            }
          ],
        quiz_answer_sno: '4',
       quiz_answer_correct: '0'
       }
      ]}
      const isDisabled =  document.getElementById(`btn-${id_1}`)
      if (isDisabled.disabled) return
      isDisabled.disabled = true
        dispatch(addQuestion(param))
        console.log(param)
        } else {
          const param = {
            quiz_id: addedQuizID,
            translation_quiz_questions: [
              {
                master_language_id: 1,
                translation_quiz_question: document.getElementById(`question-${id_1}`).value
              }
            ],
            quiz_question_duration: '30',
            quiz_question_sno: id_1,
            quiz_question_answers: [
              {
                translation_quiz_answers:[
                  {
                    // master_language_id:document.getElementById('quiz_lang').value,
                    master_language_id:1,
                    translation_quiz_answer: document.getElementById(`answer1-${id_1}`).value
                  }
                ],
              quiz_answer_sno: '1',
             quiz_answer_correct: '1'
             },
            {
              translation_quiz_answers:[
                {
                  master_language_id:1,
                  translation_quiz_answer: document.getElementById(`answer2-${id_1}`).value
                }
              ],
            quiz_answer_sno: '2',
           quiz_answer_correct: '0'
           },
           {
            translation_quiz_answers:[
              {
                master_language_id:1,
                translation_quiz_answer: document.getElementById(`answer3-${id_1}`).value
              }
            ],
          quiz_answer_sno: '3',
         quiz_answer_correct: '0'
         },
         {
          translation_quiz_answers:[
            {
              master_language_id:1,
              translation_quiz_answer: document.getElementById(`answer4-${id_1}`).value
            }
          ],
        quiz_answer_sno: '4',
       quiz_answer_correct: '0'
       }
      ]}
      const isDisabled =  document.getElementById(`btn-${id_1}`)
      if (isDisabled.disabled) return
      isDisabled.disabled = true
        dispatch(addQuestion(param))
        console.log(param)
        }
      
      }
    })
  }
  console.log(count)
  console.log(scorewinner)
  console.log(multi)
  
  console.log(addloader, addedQuizID2)
  return (
    <Fragment>
       <> 
       <div className="content-header">
         <h5 className="mb-0">Questions</h5>
        <small> Questions.</small>
     </div>
       <Form onSubmit={handleSubmit(onSubmit)}>
         <Repeater count={count}>
         
          {(i) => (
            
            <li key={i} className="list-unstyled">
              <FormGroup>
              <Label className="form-label" for="quiz_category">
              Predictor Type
            </Label>
                <Select
                  theme={selectThemeColors}
                  className='react-select'
                  classNamePrefix="select"
                  id={`item-${i}`}
                //  Value={prizeform.category}              
                  onChange={(e) => editcategory(e)}
                  options={colourOptions}
                  isClearable={false}
                />
              </FormGroup>
          {multi ?
          <div>
             <Row>
                <FormGroup tag={Col} md="12">
                  <Label className="form-label" for={`question-${i}`}>
                    Quiz question
                  </Label>
                  <Input
                    type="text"
                    id={`question-${i}`}
                    name={`question-${i}`}
                    placeholder="Who is Lionel Messi?"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`question-${i}`]
                    })}
                  />
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer1-${i}`}>
                    Answer 1
                  </Label>
                  <Input
                    type="text"
                    name={`answer1-${i}`}
                    id={`answer1-${i}`}
                    placeholder="Cricketer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer1-${i}`]
                    })}
                  />
                </FormGroup>
                <FormGroup tag={Col} md="4">
                  <div className="d-flex height-95-per align-items-end">
                    <FormGroup check inline>
                      <Label check>
                        Correct Answer
                      </Label>
                    </FormGroup>
                  </div>
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer2-${i}`}>
                    Answer2
                  </Label>
                  <Input
                    type="text"
                    name={`answer2-${i}`}
                    id={`answer2-${i}`}
                    placeholder="Footballer"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer2-${i}`]
                    })}
                  />
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer3-${i}`}>
                    Answer 3
                  </Label>
                  <Input
                    type="text"
                    name={`answer3-${i}`}
                    id={`answer3-${i}`}
                    placeholder="VolleyBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer3-${i}`]
                    })}
                  />
                </FormGroup>
              </Row>
              
                <FormGroup tag={Col} md="8">
                  <Label className="form-label" for={`answer4-${i}`}>
                    Answer 4
                  </Label>
                  <Input
                    type="text"
                    name={`answer4-${i}`}
                    id={`answer4-${i}`}
                    placeholder="BasketBaller"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer4-${i}`]
                    })}
                  />
                </FormGroup>
                
             </div> : ""}
             {scorewinner ?
              <div> 
              <Row>
                <FormGroup tag={Col} md="4">
                  <Label className="form-label" for={`answer1-${i}`}>
                   Team A Name
                  </Label>
                  <Input
                    type="text"
                    name={`answer1-${i}`}
                    id={`answer1-${i}`}
                    placeholder="Team A Name"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer1-${i}`]
                    })}
                  />
                </FormGroup>
               
              </Row>
              <Row>
                <FormGroup tag={Col} md="4">
                  <Label className="form-label" for={`answer2-${i}`}>
                  Team A Logo
                  </Label>
                  <Input
                    type="file"
                    name={`file-${i}`}
                    id={`file-${i}`}
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`-${i}`]
                    })}
                  />
                </FormGroup>
              </Row>
              <Row>
                <FormGroup tag={Col} md="4">
                  <Label className="form-label" for={`answer1-${i}`}>
                   Team B Name
                  </Label>
                  <Input
                    type="text"
                    name={`answer1-${i}`}
                    id={`answer1-${i}`}
                    placeholder="Team B Name"
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`answer1-${i}`]
                    })}
                  />
                </FormGroup>
               
              </Row>
              <Row>
                <FormGroup tag={Col} md="4">
                  <Label className="form-label" for={`answer2-${i}`}>
                  Team B Logo
                  </Label>
                  <Input
                    type="file"
                    name={`file-${i}`}
                    id={`file-${i}`}
                    innerRef={register({ required: true })}
                    className={classnames({
                      'is-invalid': errors[`-${i}`]
                    })}
                  />
                </FormGroup>
              </Row>
             
            </div> : ""
            }       
            
           <Row>
                <FormGroup tag={Col} md="6">
                  <div className="d-flex justify-content-end">
                    <Button.Ripple
                      type="button"
                      color="primary"
                     // disabled = {i === 0 }
                       id = {`btn-${i}`}
                      className="btn-next"
                      onClick={() => submitQuestion(i)}
                    >
                      <span className="align-middle d-sm-inline-block d-none">
                        Submit Questions
                      </span>
                    </Button.Ripple>
                    {addloader && (
                    <div className="ml-2 mt-2">
                      <UiLoader blocking />
                    </div>
                  )}
                  {addedQuizID2  && (
                    <p className="ml-2 pt-1 mb-0 success-msg">
                      Post Added Successfull
                    </p>
                  )}
                  </div>
                </FormGroup>
              </Row>
            </li>
          )}
        </Repeater>
        <div className="d-flex justify-content-between">
          {/* {count <= 4 && ( */}
          {count <= 6 && (
            <Button.Ripple
              className="btn-icon"
              color="primary"
              onClick={increaseCount}
            >
              <Plus size={14} />
              <span className="align-middle ml-25">Add New Question</span>
            </Button.Ripple>
          )}
        </div>
      </Form>
      </>
    </Fragment>
  )
}

export default AddQuestions

