import { useRef, useState } from 'react'
import Wizard from '@components/wizard'
// import Quizdet from "./Quizdet"
import Quizfin from "./Quizfin"
import BreadCrumbs from '@components/breadcrumbs'
const AddQuiz = () => {
  const [stepper, setStepper] = useState(null)
  const ref = useRef(null)

  const steps = [
    // {
    //   id: 'quiz-details',
    //   title: 'Quiz Details',
    //   subtitle: 'Enter Quiz Details.',
    //   content: <Quizdet stepper={stepper} />
    // },
    {
      id: 'step-questions-eng',
      title: 'Questions',
      subtitle: 'Add Questions',
      content: <Quizfin stepper={stepper} />
    }
  ]

  return (
    <div className="horizontal-wizard">
       <BreadCrumbs breadCrumbTitle='Predictor' breadCrumbParent='Predictor' breadCrumbActive='Add' />
      <Wizard instance={(el) => setStepper(el)} ref={ref} steps={steps} />
    </div>
  )
}

export default AddQuiz
